import React from 'react';
import { CopyOutlined } from '@ant-design/icons';
import { Typography, message } from 'antd';

const Experiance = ({ data }) => {
    const copyToClip = (text, what) => {

        navigator.clipboard.writeText(text);
        message.success(what + " copied successfully");
    };
    return (
        <div>


            {<p>Role: <Typography.Text type='secondary'>{data?.role}</Typography.Text> </p>}
            {data?.company?.name && <p>Company: <Typography.Text type='secondary'>{data?.company?.name}</Typography.Text> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(data?.company?.name, 'Company Name')} /></p>}
            
            {data?.company?.website_url && <p>Website: <a href={data?.company?.website_url}><Typography.Text type='secondary'>{data?.company?.website_url}</Typography.Text></a> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(data?.company?.website_url, 'Website')} /></p>}

            {data?.company?.linkedin_url && <p>LinkedIn: <a href={data?.company?.linkedin_url}> <Typography.Text type='secondary'>{data?.company?.linkedin_url}</Typography.Text> </a><CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(data?.company?.linkedin_url, 'Website')} /></p>}
        </div>
    );
}

export default Experiance;
