import { Typography, message } from 'antd';
import React from 'react';
import { CopyOutlined } from '@ant-design/icons';

const Contact = ({ data }) => {
    const copyToClip = (text, what) => {
        
        navigator.clipboard.writeText(text);
        message.success(what + " copied successfully");
      };
    return (
        <div>
            {data?.email && <p>Email: <Typography.Text type='secondary'>{data?.email}</Typography.Text> <CopyOutlined style={{cursor: "pointer"}} onClick={()=> copyToClip(data?.email, 'Email')}/></p>}
            {data?.phone && <p>Phone: <Typography.Text type='secondary'>{data?.phone}</Typography.Text> <CopyOutlined style={{cursor: "pointer"}} onClick={()=> copyToClip(data?.phone, 'Phone')}/></p>}
            {data?.website && <p>Website: <Typography.Text type='secondary'>{data?.website}</Typography.Text> <CopyOutlined style={{cursor: "pointer"}} onClick={()=> copyToClip(data?.website, 'Website')}/></p>}
            {data?.twitter && <p>Twitter: <Typography.Text type='secondary'>{data?.twitter}</Typography.Text> <CopyOutlined style={{cursor: "pointer"}} onClick={()=> copyToClip(data?.twitter, 'Twitter')}/></p>}

        </div>
    );
}

export default Contact;
