

import React from 'react';
import { CopyOutlined } from '@ant-design/icons';
import { Typography, message } from 'antd';

const Comments = ({ data }) => {
    console.log(data);
    const copyToClip = (text, what) => {

        navigator.clipboard.writeText(text);
        message.success(what + " copied successfully");
    };
    return (
        <div>
            {data?.map((item, i) =>
                <div key={i}>
                    {<p>Comment {i + 1}: <><Typography.Text type='secondary'>{item?.text}</Typography.Text></> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(item?.text, 'Comment')} /></p>}

                    {item?.url && <p>Comment URL: <a href={item?.url}><Typography.Text type='secondary'>{item?.url}</Typography.Text></a> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(item?.url, 'Comment url')} /></p>}

                    {<p>post: <a href={item?.post?.url}><Typography.Text type='secondary'>{item?.post?.url}</Typography.Text></a> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(item?.post?.url, 'Comment post')} /></p>}
                </div>)}

        </div>
    );
}

export default Comments;
