

import React from 'react';
import { CopyOutlined } from '@ant-design/icons';
import { Typography, message } from 'antd';

const Posts = ({ data }) => {
    console.log(data);
    const copyToClip = (text, what) => {

        navigator.clipboard.writeText(text);
        message.success(what + " copied successfully");
    };
    return (
        <div>
           {data?.map((item, i)=> 
           item?.url && <p key={i}>Post {i + 1}: <a href={item?.url}><Typography.Text type='secondary'>{item?.url}</Typography.Text></a> <CopyOutlined style={{ cursor: "pointer" }} onClick={() => copyToClip(item?.url, 'Website')} /></p>
           )}
            
        </div>
    );
}

export default Posts;
