import React from 'react';
import { Card, Divider, Flex, Statistic, Typography } from 'antd';

const States = ({data, loading}) => {
    return (
        <>
            <Typography.Title className='comp-title'>
                Statistic
            </Typography.Title>
            <Divider />
            <Flex gap="middle">
                <Card className='stats-card' bordered={false}>
                    <Statistic
                        title="Total accounts"
                        value={loading? "...": data?.length}
                        //   precision={2}
                        valueStyle={{
                            color: '#1677ff',
                        }}
                    // prefix={<ArrowUpOutlined />}
                    //   suffix="%"
                    />
                </Card>
                <Card className='stats-card' bordered={false}>
                    <Statistic
                        title="Scraped"
                        value={'000'}
                        //   precision={2}
                        valueStyle={{
                            color: '#3f8600',
                        }}
                    // prefix={<ArrowUpOutlined />}
                    //   suffix="%"
                    />
                </Card>
                <Card className='stats-card' bordered={false}>
                    <Statistic
                        title="Pending"
                        value={"000"}
                        
                    />
                </Card>
            </Flex>
        </>
    );
}

export default States;
